@charset "UTF-8";

/* AppleWatch Gate page */
@font-face {
	font-family: 'NanumSquare';
	font-weight: 300;
	src: url(../fonts/NanumSquareL.eot);
	src: url(../fonts/NanumSquareL.eot?#iefix) format('embedded-opentype'),
		url(../fonts/NanumSquareL.woff) format('woff');
}

@font-face {
	font-family: 'NanumSquare';
	font-weight: 400;
	src: url(../fonts/NanumSquareR.eot);
	src: url(../fonts/NanumSquareR.eot?#iefix) format('embedded-opentype'),
		url(../fonts/NanumSquareR.woff) format('woff');
}

@font-face {
	font-family: 'NanumSquare';
	font-weight: 700;
	src: url(../fonts/NanumSquareB.eot);
	src: url(../fonts/NanumSquareB.eot?#iefix) format('embedded-opentype'),
		url(../fonts/NanumSquareB.woff) format('woff');
}

@font-face {
	font-family: 'NanumSquare';
	font-weight: 800;
	src: url(../fonts/NanumSquareEB.eot);
	src: url(../fonts/NanumSquareEB.eot?#iefix) format('embedded-opentype'),
		url(../fonts/NanumSquareEB.woff) format('woff');
}

.gate_wrap {
	position: relative;
	font-size: 15px;
	color: #333;
	line-height: 1.4;
}

.gate_wrap,
.gate_wrap div,
.gate_wrap dl,
.gate_wrap dt,
.gate_wrap dd,
.gate_wrap ul,
.gate_wrap ol,
.gate_wrap li,
.gate_wrap h1,
.gate_wrap h2,
.gate_wrap h3,
.gate_wrap h4,
.gate_wrap h5,
.gate_wrap h6,
.gate_wrap pre,
.gate_wrap form,
.gate_wrap fieldset,
.gate_wrap input,
.gate_wrap select,
.gate_wrap textarea,
.gate_wrap p,
.gate_wrap blockquote,
.gate_wrap th,
.gate_wrap td,
.gate_wrap button {
	font-family: Roboto, Droid Sans, sans-serif, "Malgun Gothic", Helvetica, Apple-Gothic, "애플고딕", Tahoma, dotum, "돋움", gulim, "굴림";
}

.gate_wrap li,
.gate_wrap p,
.gate_wrap dt,
.gate_wrap dd,
div,
.gate_wrap span,
.gate_wrap td,
.gate_wrap th {
	word-break: keep-all;
	word-wrap: normal;
}

.gate_wrap .center {
	text-align: center;
}

.gate_wrap .h10 {
	margin-top: 10px;
}

.gate_wrap .h30 {
	margin-top: 30px;
}

.gate_wrap .fSize12 {
	font-size: 12px !important;
}

.gate_wrap .fColor1 {
	color: #e3002b !important;
}

.gate_wrap img {
	display: block;
	width: 100%;
	max-width: 100%;
}

.gateSec.keyconceptSec {
	position: relative;
	padding-bottom: 80px;
	background-color: #f6f6f6;
}

.gateSec.keyconceptSec .txtCaution {
	position: absolute;
	bottom: 20px;
	left: 0;
	width: 100%;
	padding: 0 20px;
	font-size: 9px;
	color: #888;
}

.gateSec.keyconceptSec .txtCaution strong {
	font-size: 10px;
}

.gateSec .secCont {
	position: relative;
	padding: 0 10px;
}

.gateSec h3 {
	padding-top: 30px;
	font-family: 'NanumSquare', sans-serif;
	font-size: 28px;
	color: #111;
	letter-spacing: -1px;
	font-weight: 300;
	line-height: 1.2;
}

.gateSec h3 strong {
	font-weight: 800;
}

.detail .desc {
	margin-top: 5px;
	font-size: 12px;
	color: #333;
}

.gateSec .list_bul {
	margin-top: 10px;
}

.gateSec .list_bul li {
	position: relative;
	margin-top: 5px;
	padding-left: 6px;
	font-size: 10px;
	color: #888;
	letter-spacing: -1px;
}

.gateSec .list_bul li:before {
	display: block;
	position: absolute;
	top: 5px;
	left: 0;
	width: 2px;
	height: 2px;
	background-color: #888;
	content: '';
}

.detail .linkTxt {
	display: block;
	margin-top: 15px;
	font-family: 'NanumSquare', sans-serif;
	color: #0070c9;
	font-weight: 700;
}

.detailCont3+.img {
	padding-bottom: 30px;
}

.secCont.detail.faq {
	text-align: center;
}

.secCont.detail.faq h3 {
	padding-top: 108px;
	background: url(../img/event_2019/gate/bg_faq_tit.png) no-repeat 50% 40px;
	background-size: 70px 49px;
}

#footerWrap {
	margin-top: 40px;
}

.inPop:after {
	display: block;
	/* height: 30px; */
	content: '';
}

.inPop h3 {
	font-family: 'NanumSquare', sans-serif;
	font-size: 20px;
	color: #111111;
	letter-spacing: -1px;
	font-weight: 300;
	line-height: 1.2;
}

.inPop h3 strong {
	font-size: 25px;
	font-weight: 800;
}

#faq_wrap .faq_select {
	border: 3px solid #333;
}

#faq_wrap .faq_select button {
	width: 100%;
	padding: 14px 40px 18px 13px;
	font-size: 15px;
	font-weight: bold;
	text-align: left;
	color: #333;
	background: #fff url(../img/event_2019/gate/bg_faq_tgl.png) no-repeat 97% 3px / 25px 92px;
	-webkit-appearance: none;
	vertical-align: middle;
}

#faq_wrap .faq_select ul {
	display: none;
	padding: 0 28px;
}

#faq_wrap .faq_select ul li {
	display: block;
	border-top: 1px solid #ddd;
}

#faq_wrap .faq_select ul li:first-child {
	border-top: 0;
}

#faq_wrap .faq_select ul li a {
	display: block;
	padding: 14px 2px;
	color: #666;
	font-size: 12px;
}

#faq_wrap .faq_select ul li a.on {
	color: #333;
	font-weight: bold;
}

dl.qna_list {
	margin-top: 15px;
	border-bottom: 1px solid #ddd;
}

dl.qna_list dt {
	padding: 18px 0;
	border-top: 1px solid #ddd;
}

dl.qna_list dt a {
	position: relative;
	display: block;
	color: #666666;
	font-size: 13px;
	line-height: 140%;
	padding: 0px 40px 0px 16px;
}

dl.qna_list dt a.on {
	color: #111;
	font-weight: bold;
}

dl.qna_list dt a:before {
	content: ' ';
	display: block;
	width: 11px;
	height: 11px;
	position: absolute;
	top: 3px;
	left: 0px;
	background: url(../img/event_2019/gate/txt_q.png) no-repeat left top/11px 110px;
}

dl.qna_list dt a:after {
	content: '';
	display: block;
	width: 12px;
	height: 7px;
	position: absolute;
	top: 3px;
	right: 0px;
	background: url(../img/event_2019/gate/faq_arrow.png) no-repeat left top/12px 110px;
}

dl.qna_list dt a.on:before,
dl.qna_list dt a.on:after {
	background-position: left bottom;
}

dl.qna_list dd {
	display: none;
	padding: 0px 0px 18px 15px;
	color: #666666;
	font-size: 12px;
	background: url(../img/event_2019/gate/txt_a.png) no-repeat left 5px/11px 9px;
}